//  
import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'
import { ToDo } from '../models/todo-item'
import { v4 as uuid} from 'uuid'

//     
export interface ToDoState {
   todos: ToDo[],
   todosTrue: number,
   todosFlse: number,
}
//  ,  
const initialState: ToDoState = {
   todos: [],
   todosTrue: 0,
   todosFlse: 0,
}
//  ,     
export const todoSlice = createSlice({
   name: 'todoList',
   initialState,
   reducers: { //  ,      (state)   
      craeteAction: (state, action: PayloadAction<string>) => { //action -    
         const newToDo: ToDo = {
            id: uuid(),
            text: action.payload, //    ,  action.payload._
            isDone: false
         }
         state.todos = [...state.todos, newToDo] //  state.todos
      },
      updateAction: (state, action: PayloadAction<ToDo>) => {
         const newTodos = state.todos.map((todo) => {
            if (todo.id === action.payload.id) {
               todo.isDone = !todo.isDone;
            }
            return todo;
         });
         state.todos = newTodos //  state.todos
      },
      deleteAction: (state, action: PayloadAction<ToDo>) => {
         const newTodos = state.todos.filter((todo) => todo.id !== action.payload.id);
         state.todos = newTodos
      },
   },
})

export const { craeteAction, updateAction, deleteAction } = todoSlice.actions

export default todoSlice.reducer

